/*------------
Global Styles
-------------*/
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body{
	margin: 1rem;
}

/*------------
BODY
-------------*/

.grid-list{
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	
}

.grid-item-container{
	display: flex;
	align-content: center;
	justify-content: center;
	width: 200px;
	height: 200px;
	background-color: black;
	margin:1.5rem;
}

.grid-list--item img{
	width: 200px;
	height: auto;
}

.item-name{
	color: #FFF;
	text-shadow: 2px 2px #272727;
	position: absolute;
	margin: auto 0;
}


/*------------
MODAL
-------------*/

.modal{
	background-color: rgba(0,0,0,0.4);
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 5000;
  
  }
  .modal-content{
    animation-duration: 0.3s;
    animation-name: modalopen;
    background-color: #282a36;
    box-shadow: 5px 5px 15px rgba(0,0,0,0.1),
    -5px -5px 15px rgba(0,0,0,0.1);
    height: auto;
    overflow: auto;
    width: 96vw;
  }

.hidden{
	display: none;
}